import styled from 'styled-components'

// TODO: configurar variaveis e parametrizar atributos de %
export const Container = styled.div`
    background: linear-gradient(
        ${(props) => props.theme.colors.gradient_1} 0%,
        ${(props) => props.theme.colors.gradient_2} 57%, 
        ${(props) => props.theme.colors.gradient_3} 57%,
        ${(props) => props.theme.colors.gradient_4} 100%
    );
`

export const Circle = styled.div`
     {
        display: inline-block;
        position: relative;
    }
    img {
        display: block;
    }
    :after {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        /* TODO: configurar e parametrizar gradiente daqui */
        background: linear-gradient(
            rgba(0, 0, 0, 1) 100%,
            rgba(0, 0, 0, 0) 100%,
            rgba(0, 0, 0, 0) 30%,
            rgba(0, 0, 0, 1) 100%
        );
        position: absolute;
    }
`

export const ConfiguredImage = styled.img`
    object-fit: contain;

    .overlay {
        color: black;
    }
`
