import styled from 'styled-components'

export const Bio = styled.div`
    * {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 1rem;
        font-size: 0.8rem;
        font-family: ${(props) => props.theme.fonts.regularTextFont}
    },

    .container {
        max-width: 80%;
    },

    .title {
        font-family: ${(props) => props.theme.fonts.titleFont};
        font-size: 2.5rem;
    },

    .text {
        text-align: justify;
        color: ${(props) => props.theme.colors.mainText};
    },

    .player {
        max-width: 90%;
        /* max-height: 80vh; */
    }
`
