import { createGlobalStyle } from 'styled-components'
import mainPattern from '../assets/backgrounds/mainPattern.svg'

export default createGlobalStyle`
    * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        outline: 0;
    }    

    .section {
        display: flex;
        flex-direction: column;
        align-items: center;
        min-width: 100vw;
        height: 100vh;
        background-image: 
            url(${mainPattern}),
            linear-gradient(
                ${(props) => props.theme.colors.gradient_1} 0%,
                ${(props) => props.theme.colors.gradient_2} 5%,
                ${(props) => props.theme.colors.gradient_3} 57%,
                ${(props) => props.theme.colors.gradient_4} 100%
            );
    }    
`
