import React from 'react'
import { Bio } from './style'
import ReactPlayer from 'react-player'

const Biography = () => {
    return (
        <div>
            <Bio className="section">
                <div className="container">
                    <h2 className="title">Biografia</h2>
                    <p className="text">
                        DJ Sandinho é um nome conhecido no mundo da música,
                        especialmente no universo do funk carioca. Apelidado de
                        O Alquimista das Aceleradas, ele é conhecido por suas
                        misturas incríveis e seu ritmo louco.
                        <br />
                        <br />
                        Seu amor pelo drum n bass é evidente em suas
                        apresentações, onde ele mistura batidas rápidas e
                        intensas com a técnica de mixagem que domina. Com anos
                        de experiência na indústria da música, DJ Sandinho é um
                        mestre em seu ofício. Sua técnica de mixagem é
                        incomparável, e ele é capaz de transformar qualquer
                        música em uma experiência mágica no verdadeiro baile.
                        Sua paixão pela música e sua habilidade em criar magias
                        sonoras são evidentes em todas as suas apresentações. Se
                        você ainda não teve a oportunidade de experimentar a
                        música de DJ Sandinho, não perca mais tempo. Ele é um
                        artista único, capaz de transformar qualquer evento em
                        um verdadeiro baile. Com seu estilo inovador e sua
                        habilidade técnica, DJ Sandinho é um dos artistas mais
                        talentosos e promissores da cena musical atual. Não
                        deixe de conferir seu próximo show!
                    </p>
                </div>
            <ReactPlayer className="player" url='https://www.youtube.com/watch?v=QyGY5uJRVV8' />
            </Bio>
        </div>
    )
}

export default Biography
