import styled from 'styled-components'

export const HomeStyle = styled.div`
    .img-section {
        max-width: 94vw;
    }


    @keyframes spawnLogo {
        0%   {max-height: 0%;}
        100% {max-height: 20%;}
    }

    .logo-section {
        max-height: 20%;
        animation-name: spawnLogo;
        animation-duration: 3s;
    }
`
